
.container.container.container_testimonial{
    width: 40%;
    margin-bottom: 4rem;
}

.client_image{
    width: 4rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid var(--color-primary-variant);
}

.testimonial{
    
    background: var(--color-primary-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}

.client_review{
    color: var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}

@media screen and(max-width: 1024px) {
    .container_testimonial{
        width: 60%;
    } 
}

@media screen and(max-wid) {

    .container_testimonial{
        width: var(--container-width-sm);
    }

    .client_review{
        width: var(--container-width-sm);
    }
}